/* 48em = 768px */
@media (min-width: 768px) {
  .header li {
    float: left;
  }
  .header {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .header .menu {
    max-height: none;
  }
  .header .menu-icon {
    display: none;
  }
}
@media (max-width: 1155px) {
  .header .menu>li{
    margin: 10px 0px;
  }
  .header li a {
    padding: 10px 15px;
  }
}

@media (max-width: 975px) and (min-width: 768px) {
  .header {
    font-size: 13px;  
    max-width: 95%;
    padding: 15px 5px;
  }
  .header li a {
    padding: 10px 15px;
  }
}

@media (max-width: 768px) {
  .header .logo { 
    font-size: 1.25em;
    margin-top: 12px;
  }

  .header {
